<template>
  <div>
    low的一种方式
    <input
      type="text"
      :value="currentValue"
      @input="handleInput"
    >
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      currentValue: this.value
    }
  },
  methods: {
    handleInput (event) {
      this.$emit('input', event.target.value);
    }
  }
}
</script>

